<template>
	<view class="wrapper">
		<view class="qrcode">
			<Qrcode
				cid="qrcode1"
				ref="qrcode"
				:val="val"
				:size="size"
				:unit="unit"
				:background="background"
				:foreground="foreground"
				:pdground="pdground"
				:icon="icon"
				:iconSize="iconsize"
				:lv="lv"
				:onval="onval"
				:loadMake="loadMake"
				:usingComponents="true"
				@result="qrR"
			></Qrcode>
		</view>
		<view class="text-tips">
			<text>注册有好礼</text>
			<text>扫一扫有惊喜</text>
		</view>
		<u-modal v-model="show" :content="content"></u-modal>
		<view class="btn"><u-button shape="circle" type="success" @click="saveQrCode">保存二维码</u-button></view>
	</view>
</template>

<script>
import Qrcode from '@/components/tki-qrcode/tki-qrcode.vue';

export default {
	components: { Qrcode },
	data() {
		return {
			val: '赚钱提现，你就是大富豪！', // 要生成的二维码值
			size: 400, // 二维码大小
			unit: 'upx', // 单位
			background: '#FFFfff', // 背景色
			foreground: '#000000', // 前景色
			pdground: '#000000', // 角标色
			icon: '../../static/logo.png', // 二维码图标
			iconsize: 40, // 二维码图标大小
			lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
			onval: false, // val值变化时自动重新生成二维码
			loadMake: true, // 组件加载完成后自动生成二维码
			src: '', // 二维码生成后的图片地址或base64
			show: false,
			content: '保存成功！'
		};
	},
	methods: {
		qrR(res) {
			this.src = res;
		},
		saveQrCode() {
			console.log(this.src);
			// #ifdef H5
			const a = document.createElement('a')
			// 创建一个单击事件
			const event = new MouseEvent('click')
			a.download = 'qrcode.png';
			a.href = this.src;
			
			// 触发a的单击事件
			a.dispatchEvent(event)
			// #endif

			// #ifdef APP-PLUS
			this.save();
			// #endif

			// #ifdef MP
			this.save();
			// #endif
		},
		save() {
			uni.saveImageToPhotosAlbum({
				filePath: this.src,
				success: () => {
					this.show = true;
				},
				fail: (err) => {
					console.log(err)
				}
			});
		}
	}
};
</script>

<style lang="scss">
page {
	height: 100%;
	.wrapper {
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		.btn {
			margin-top: 200upx;
		}
		.text-tips {
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			margin: 30upx 30upx;
			text {
				&:first-child {
					font-size: 32upx;
					font-weight: bold;
					margin-bottom: 30upx;
				}
			}
		}
	}
}
</style>
